import React from 'react'
import cx from 'classnames'
import './FlipCard.scss'

export default class FlipCard extends React.PureComponent {
  static defaultProps = {
  }
  constructor (props) {
    super(props)
    this.state = {
      children: props.children,
      nextChildren: null
    }
    this.root = React.createRef()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.dataKey !== this.props.dataKey || !this.props.children) {
      const {delay, children} = this.props
      this.setState({
        nextChildren: children
      }, () => {
        this.root.current.style.transform = 'rotateX(-180deg)'
        this.root.current.style.transition = `transform 1000ms ease ${delay}`
        const transitionEnd = () => {
          this.root.current.removeEventListener('transitionend', transitionEnd)
          this.setState({
            children,
            nextChildren: null
          })
          this.root.current.style.transform = ''
          this.root.current.style.transition = ''
        }
        this.root.current.addEventListener('transitionend', transitionEnd, {once: true})
      })
    }
  }

  render () {
    const {props, state} = this

    return (
      <div className={cx('dui-flip-card', props.className)} style={props.style}>
        <div ref={this.root} className='dui-flip-card-inner'>
          <div className='dui-flip-card-front'>{state.children}</div>
          <div className='dui-flip-card-back'>{state.nextChildren}</div>
        </div>
      </div>
    )
  }
}
