import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import * as d3 from 'd3'
import { thousandComma } from '../../lib/numberCore'
import FlipCard from '../../hoc/FlipCard'
import './style.scss'

export default class RankScroller extends React.Component {
  static propTypes = {
    data: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      value: PropTypes.number.isRequired
    })).isRequired
  }

  static defaultProps = {
    data: [],
    showCount: 5,
    shouldShowCount: true,
    highlightCount: 3,
    interval: 5000,
    unit: '',
    min: 16,
    max: 100,
    nameInNewLine: false,
    rankStyle: {
      color: '#00AAFF',
      fontSize: 12
    },
    nameStyle: {
      color: '#00AAFF',
      fontSize: 12
    }
  }

  state = {dataChangeCount: 0}

  componentDidMount () {
    this.initData()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.data !== this.props.data) {
      this.initData()
    }
  }

  initData = () => {
    let { data, showCount, min, max } = this.props

    let yScale = d3.scaleLinear().domain(d3.extent(data, v => v.value)).range([min, max])

    this.setState({
      showIndex: 0,
      showData: data.slice(0, showCount),
      yScale,
      dataChangeCount: this.state.dataChangeCount + 1
    }, this.scrollData)
  }

  scrollData = () => {
    clearInterval(this.timerKey)
    const {showCount, data} = this.props
    if (data.length <= showCount) {
      return
    }
    this.timerKey = setInterval(() => {
      const { props, state } = this
      const nextShowIndex = state.showIndex + showCount >= props.data.length ? 0 : state.showIndex + showCount
      let showData = props.data.slice(nextShowIndex, nextShowIndex + showCount)
      this.setState({
        showData,
        showIndex: nextShowIndex
      })
    }, this.props.interval)
  }

  componentWillUnmount () {
    clearInterval(this.timerKey)
  }

  render () {
    const { props, state } = this
    const { showCount, unit, highlightCount, shouldShowCount } = props
    const { showIndex, showData, dataChangeCount } = state
    if (!showData || showData.length === 0) return null

    return (
      <div style={props.style} className={cx('dui-rank-scroller', props.className)}>
        {
          Array.from({ length: showCount }).map((a, i) => {
            let v = showData[i]

            let widthPercent = v ? state.yScale(v.value) : 0
            let rank = showIndex + i
            return (
              <FlipCard key={i} className='dui-rank-scroller-filpper' delay={`${i * 300}ms`} dataKey={dataChangeCount + '_' + rank}>
                {
                  v
                    ? <div className='item'>
                      <div className={cx('item-header', { active: rank < highlightCount })} style={props.rankStyle}>
                        <span className='item-rank'>NO.{rank + 1}</span>
                        {
                          !props.nameInNewLine &&
                          <span className={cx('item-name', { active: rank < highlightCount })} style={props.nameStyle}>{v.name}</span>
                        }
                        {
                          shouldShowCount && <span className='item-value'>{thousandComma(v.value)}{unit}</span>
                        }
                      </div>
                      {
                        props.nameInNewLine &&
                        <div className={cx('item-name', { active: rank < highlightCount })} style={props.nameStyle}>{v.name}</div>
                      }
                      <div className='item-content'>
                        <div style={{ width: widthPercent + '%' }} className={cx('item-content-bar', { active: rank < highlightCount })} />
                      </div>
                    </div> : null
                }
              </FlipCard>
            )
          })
        }
      </div>
    )
  }
}
